import { EXButton } from './components'

// 
const components = [
  EXButton
]

//  install  Vue  use 
const install = function(Vue) {
  // 
  // @ts-ignore
  if (install.installed) return
  // 
  components.map(component => Vue.component(component.name, component))
}

// 
// @ts-ignore
if (typeof window !== 'undefined' && window.Vue) {
  // @ts-ignore
  install(window.Vue)
}

export default {
  //  install Vue.use() 
  install,
  // 
  EXButton
}
